/* Background Layer */
.background-layer {
  position: fixed; /* Ensures it stays static as the last layer */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; /* Ensures it’s behind the content */
  overflow: hidden; /* Prevent scrollbars */
  pointer-events: none; /* Prevent interaction with the background */
}

/* Background Animation */
#background {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100%;
  background: #000; /* Base background color */
  overflow: hidden;
}

#background span {
  position: absolute;
  display: block;
  width: 50px;
  height: 50px;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5),
              0 0 20px rgba(255, 255, 255, 0.4),
              0 0 40px rgba(255, 255, 255, 0.3);
  animation: move 15s linear infinite;
}

#background span:nth-child(2) { animation-duration: 20s; }
#background span:nth-child(3) { animation-duration: 25s; }
#background span:nth-child(4) { animation-duration: 18s; }
#background span:nth-child(5) { animation-duration: 22s; }
#background span:nth-child(6) { animation-duration: 30s; }
#background span:nth-child(7) { animation-duration: 24s; }
#background span:nth-child(8) { animation-duration: 28s; }

/* Scrolling Effect */
@keyframes move {
  0% {
    transform: translateY(0) scale(1);
    opacity: 1;
  }
  100% {
    transform: translateY(-150vh) scale(0.5);
    opacity: 0;
  }
}
